import StatTesting from './statTesting';

class HandleRawData {
    handleConf(conf) {
        let confidence = new StatTesting();
        let level = confidence.confidenceLevels()[conf.conf];
        return level;
    }

    handleBases(bases) {
        let basesArray = bases.bases.split(/\t+/g);
        basesArray = basesArray.map(x => x.replace(/\D+/g, '')).map(Number);
        return basesArray;
    }

    handleDrivers(drivers) {
        // drivers are provided optional so check if available
        if (!drivers.drivers) {
            return "";
        }
        let driversArray = drivers.drivers.split(/\n/g);
        driversArray = driversArray.filter(x => x !== '');
        return driversArray;
    }

    handlePercents(percents) {
        let percentsArray = percents.percents.split(/\n/g);
        percentsArray = percentsArray.filter(x => x !== '');

        // check if the source data includes '%' sign and get only them if yes
        let isPercentsBasedFile = percentsArray.filter(x => x.includes('%')).length > 0 ? true : false;
        if (isPercentsBasedFile) {
            percentsArray = percentsArray.filter(x => x.includes('%'));
        } else {
            percentsArray = percentsArray.filter(x => /\d/.test(x));
        }

        // create matrix and remove '%' symbol
        let percentsMatrix = percentsArray.map(x => x.split(/\t+/g).map(y => y.replace(/[%]/g, '').trim()));
        // replace '-' only cells and map to number
        percentsMatrix = percentsMatrix.map(x => x.map(y => {
            if (y === '-') {
                y = -100;
            } else {
                y /= 100;
            }
            y = +y;
            return y;
        }));

        return percentsMatrix;
    }
}

export default HandleRawData;
