import React, { useState } from 'react';
import './MultiTable.css'

import TableRow from './TableRow/TableRow';

const MultiTable = (props) => {
    const [styles, setStyles] = useState({
        s: [],
        i: null
    });

    const showSigLetters = (i, event) => {
        let alphabet = props.data.data.sequence;
        let rowSelected = i;
        let columnSelected = event.target.cellIndex - 1;

        let targetRow = props.data.data.testedPercents[rowSelected];
        let targetLetter = String.fromCharCode(65 + columnSelected)
        
        let stylesArray = [];
        for (let i = 0; i < targetRow.length; i++) {
            if (targetRow[i].includes(targetLetter)) {
                stylesArray.push('green');
            } else if (targetRow[columnSelected].includes(alphabet[i])) {
                stylesArray.push('red');
            } else {
                stylesArray.push('yellow');
            }
        }
        setStyles({
            s: stylesArray,
            i: rowSelected
        });
    }

    const hideSigLetters = (event) => {
        setStyles({
            s: [],
            i: null
        });
    }

    return (
        <table className="MultiTable">
            <thead>
                <tr>
                    <th>Drivers</th>
                    { props.data.data.bases.map((b, i) => {
                            let lowBase = false;
                            if (b < 50) {
                                b = b + " *";
                                lowBase = true;
                            }
                            return <th key={i} className={lowBase ? 'lowbase' : null}>{b}</th>
                        })
                    }
                </tr>
                <tr>
                    <th>Stat letters</th>
                    { props.data.data.sequence.map((s, i) => <th key={i}>{s}</th>) }
                </tr>
            </thead>

            <tbody>
                { props.data.data.testedPercents.map((r, i) => {
                    let st = {}
                    if (styles.i === i) {
                        st = styles;
                    } else {
                        st.s = [];
                    }
                    
                    return (
                        <TableRow
                            key={i}
                            showStatTest={showSigLetters.bind(this, i)}
                            hideStatTest={hideSigLetters}
                            sigStyles={st}
                            driverValue={props.data.data.drivers[i]}
                            percentsValues={r}
                            idx={i}
                        />
                    )
                }) }
            </tbody>
        </table>
    )
}

export default MultiTable;
