import React from 'react';
import './TableRow.css';

const TableRow = (props) => {
    return (
        <tr>
            <td key={props.idx}>{props.driverValue || "<->"}</td>
            { 
                props.percentsValues.map((r, i) => {
                    if (props.sigStyles.s.length > 0) {
                        return <td 
                                key={i} 
                                className={props.sigStyles.s[i]} 
                                onMouseOver={props.showStatTest}
                                onMouseLeave={props.hideStatTest}
                                >{r}</td>;
                    } else {
                        return <td 
                                key={i} 
                                onMouseOver={props.showStatTest}
                                onMouseLeave={props.hideStatTest}
                                >{r}</td>;
                    }
                })
            }
        </tr>
    )
}

export default TableRow;
