import HandleRawData from './handleRawData';
import AppendLetters from './appendLetters';

class FinalData {
    processData(conf, bases, drivers, percents) {
        let handleRawData = new HandleRawData();
        let fineConf = handleRawData.handleConf(conf);
        let fineBases = handleRawData.handleBases(bases);
        let fineDrivers = handleRawData.handleDrivers(drivers);
        let finePercents = handleRawData.handlePercents(percents);
        
        let letters = new AppendLetters();
        let withLetters = letters.addStatLetters(fineConf, fineBases, finePercents);
        let lettersSequence = letters.generateAlphabet(fineBases);
        
        return {
            bases: fineBases,
            drivers: fineDrivers,
            testedPercents: withLetters,
            sequence: lettersSequence
        }
    }
}

export default FinalData;
