import React, { useState } from 'react';
import './MultiTest.css';

import MultiForm from '../../components/MultiForm/MultiForm';
import MultiTable from '../../components/MultiTable/MultiTable'
import DataService from '../../services/finalData';

const MultiTest = () => {
    const [conf, setConf] = useState({
        conf: "95%"
    });
    const [bases, setBases] = useState({
        bases: ""
    });
    const [drivers, setDrivers] = useState({
        drivers: ""
    });
    const [percents, setPercents] = useState({
        percents: ""
    });
    const [isValid, setisValid] = useState({
        valid: false
    });
    const [data, setData] = useState({
        data: {}
    });

    const handleChange = (event) => {
        if (event.target.name === "conf") {
            setConf({
                conf: event.target.value
            });
        } else if (event.target.name === "bases") {
            setBases({
                bases: event.target.value
            });
        } else if (event.target.name === "drivers") {
            setDrivers({
                drivers: event.target.value
            });
        } else if (event.target.name === "percents") {
            setPercents({
                percents: event.target.value
            });
        }
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        let dataService = new DataService();
        let wholeData = dataService.processData(conf, bases, drivers, percents);
        setisValid({ valid: true });
        setData({data: wholeData})
    }

    return (
        <section className="MultiTesting">
            <MultiForm formSubmited={handleSubmit} formChanges={handleChange} />
            {
                isValid.valid ?
                    <MultiTable data={data} /> :
                    null
            }
        </section> 
    )
}

export default MultiTest;
