class StatTesting {
    confidenceLevels() {
        return {
            '60%': 0.842,
            '70%': 1.036,
            '80%': 1.282,
            '85%': 1.44,
            '90%': 1.645,
            '95%': 1.96,
            '98%': 2.326,
            '99%': 2.576,
            '99.8%': 3.091,
            '99.9%': 3.291,
        }
    }

    calcStatTest(base1, base2, perc1, perc2, letterForColumn, confScore) {
        let P = ((base1 * perc1) + (base2 * perc2)) / (base1 + base2);
        let Q = 1 - P;
        let Z = Math.abs((perc1 - perc2) / Math.sqrt(P * Q * ((1 / base1) + (1 / base2))));
        if (Z > confScore) {
            
            if (perc1 > perc2) {
                return String.fromCharCode(65 + letterForColumn)
            } else {
            return ""
            }
        }
        else {
            return ""
        }
    }
}

export default StatTesting;
