import StatTesting from './statTesting';

class AppendLetters {
    addStatLetters(conf, bases, percents) {
        let testing = new StatTesting();

        let resultMatrix = [];
        for (let row = 0; row < percents.length; row++) {

            let result = [];
            for (let col = 0; col < percents[0].length; col++) {
                let str = '';
                for (let c = 0; c < percents[0].length; c++) {
                    if (col === c) {
                        continue;
                    }
                    str += testing.calcStatTest(bases[col], bases[c], percents[row][col], percents[row][c], c, conf);
                }
                if (percents[row][col] === -100) {
                    result.push('-')
                } else {
                    result.push(parseFloat((percents[row][col] * 100).toFixed(5)) + '% ' + str);
                }
            }
            resultMatrix.push(result);
        }
        
        return resultMatrix;
    }

    generateAlphabet(bases) {
        return bases.map((x, i) => String.fromCharCode(65 + i));
    }
}

export default AppendLetters;
